import { Component, OnInit } from '@angular/core';
import { ModalController, AlertController } from '@ionic/angular';
import { InicioService } from '../../../providers/Almacenamiento/inicio.service';
import { InventariosService } from '../../../providers/Api/Inventarios.service';
import { IvtModalPage } from '../ivt-modal/ivt-modal.page';
import { ToastServices } from '../../../providers/Utilidades/ToastService.service';
import { trigger, transition, style, animate } from '@angular/animations';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-ivtproveedores',
  templateUrl: './ivtproveedores.page.html',
  styleUrls: ['./ivtproveedores.page.scss'],
  animations: [
    trigger('salida', [
      transition(':leave', [
        style({
          transform: 'scale(1)',
          opacity: 1
        }),
        animate('300ms', style({
          transform: 'scale(0)',
          opacity: 0
        }))
      ])
    ])
  ]
})
export class IVTProveedoresPage implements OnInit {

  proveedores = [];
  load = false;

  constructor(
    private modalCtrl: ModalController,
    private almacenamiento: InicioService,
    private ivtApi: InventariosService,
    private toast: ToastServices,
    private alertCtrl: AlertController,
    private activeRoute: ActivatedRoute
  ) { }

  ngOnInit() {
  }

  ionViewWillEnter() {
    this.cargarProveedores();
  }

  refrescando( event ) {
    this.cargarProveedores().then(() => {
      event.target.complete()
    }).catch(() => event.target.complete())
  }

  async cargarProveedores() {
      const info = await this.almacenamiento.obtenerToken();

      if ( info ) {
        this.ivtApi.leerProveedor(info.token, this.activeRoute.snapshot.paramMap.get('id')).subscribe((data: any) => {
          if (data.exe) {
              this.proveedores = data.response;
          } else {
            this.toast.toastError('No se pudo cargar los datos, intentelo más tarde');
          }
        }, (err) => {
            this.toast.toastError('No se pudo cargar los datos, intentelo más tarde');
        })
      }
  }

  async modal() {
    const modal = await this.modalCtrl.create({
      component: IvtModalPage,
      componentProps: {
        tipo: 'proveedores',
        inventario: this.activeRoute.snapshot.paramMap.get('id')
      },
    })

    await modal.present();

    const cerrado = await modal.onWillDismiss();

    if (cerrado.data) {
      if (cerrado.data.exe) {
        this.cargarProveedores();
      }
    }
  }

  async editar ( proveedor: any, i ) {
      const alert = await this.alertCtrl.create({
        header: 'Editar Proveedor',
        inputs: [
          {
            type: 'text',
            name: 'nombre',
            placeholder: 'Escriba el nombre del proveedor',
            value: proveedor.nombre
          },
          {
            type: 'number',
            name: 'tel1',
            placeholder: 'Teléfono de contacto',
            value: proveedor.telefono
          }, 
          {
            type: 'number',
            name: 'tel2',
            placeholder: 'Teléfono de contacto adicional',
            value: proveedor.telefono2
          },
          {
            type: 'text',
            name: 'correo',
            placeholder: 'Correo electrónico de contacto',
            value: proveedor.correo
          }
        ],
        buttons: [
          {
            text: 'Cancelar',
            role: 'cancel'
          },
          {
            text: 'Aceptar',
            handler: async (data) => {
                const info = await this.almacenamiento.obtenerToken();

                if ( info ) {
                  // tslint:disable-next-line:max-line-length
                  this.ivtApi.editarProveedor(info.token, {nombre: data.nombre, tel1: data.tel1, tel2: data.tel2, correo: data.correo, _id: proveedor._id}).subscribe((res: any) => {
                      
                      if ( res.exe ) {
                        this.toast.toastExitoso('Proveedor editado!');
                        this.proveedores[i].nombre = res.response.nombre;
                        this.proveedores[i].telefono = res.response.telefono;
                        this.proveedores[i].telefono2 = res.response.telefono2;
                        this.proveedores[i].correo = res.response.correo;
                      } else {
                        this.toast.toastError('No se pudo editar el proveedor, intentelo más tarde');
                      }
                  }, (err) => {
                    this.toast.toastError('No se pudo editar el proveedor, intentelo más tarde');
                  })
                }
            }
          }
        ]
      })

      await alert.present();
  }

  async eliminar ( proveedor: any, i ) {
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
      // tslint:disable-next-line:max-line-length
      this.ivtApi.eliminarProveedor(info.token, proveedor._id).subscribe((res: any) => {
          
          if ( res.exe ) {
            this.toast.toastExitoso('Proveedor eliminado!');
            this.proveedores.splice(i, 1);
          } else {
            this.toast.toastError('No se pudo eliminar el proveedor, intentelo más tarde');
          }
      }, (err) => {
        this.toast.toastError('No se pudo eliminar el proveedor, intentelo más tarde');
      })
    }
  }

}
